 }
/* animaciones => OneCard */

.poke_box {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
}

.pokeball {
  position: relative;
  width: 150px;
  height: 150px;
  background: #fff;
  border: 5px solid #000;
  border-radius: 50%;
  overflow: hidden;
  box-shadow: inset -10px 10px 0 10px #ccc;
  -webkit-animation: shake 1.25s cubic-bezier(0.36, 0.07, 0.19, 0.97) infinite;
          animation: shake 1.25s cubic-bezier(0.36, 0.07, 0.19, 0.97) infinite;
  -webkit-animation-play-state: paused;
          animation-play-state: paused;
  cursor: pointer;

}

.pokeball:hover {
  -webkit-animation-play-state: running;
          animation-play-state: running;
}

@-webkit-keyframes shake {
  0% {
    -webkit-transform: translate(0, 0) rotate(0);
            transform: translate(0, 0) rotate(0);
  }
  20% {
    -webkit-transform: translate(-10px, 0) rotate(-20deg);
            transform: translate(-10px, 0) rotate(-20deg);
  }
  30% {
    -webkit-transform: translate(10px, 0) rotate(20deg);
            transform: translate(10px, 0) rotate(20deg);
  }
  50% {
    -webkit-transform: translate(-10px, 0) rotate(-10deg);
            transform: translate(-10px, 0) rotate(-10deg);
  }
  60% {
    -webkit-transform: translate(10px, 0) rotate(10deg);
            transform: translate(10px, 0) rotate(10deg);
  }
  100% {
    -webkit-transform: translate(0, 0) rotate(0);
            transform: translate(0, 0) rotate(0);
  }
}

@keyframes shake {
  0% {
    -webkit-transform: translate(0, 0) rotate(0);
            transform: translate(0, 0) rotate(0);
  }
  20% {
    -webkit-transform: translate(-10px, 0) rotate(-20deg);
            transform: translate(-10px, 0) rotate(-20deg);
  }
  30% {
    -webkit-transform: translate(10px, 0) rotate(20deg);
            transform: translate(10px, 0) rotate(20deg);
  }
  50% {
    -webkit-transform: translate(-10px, 0) rotate(-10deg);
            transform: translate(-10px, 0) rotate(-10deg);
  }
  60% {
    -webkit-transform: translate(10px, 0) rotate(10deg);
            transform: translate(10px, 0) rotate(10deg);
  }
  100% {
    -webkit-transform: translate(0, 0) rotate(0);
            transform: translate(0, 0) rotate(0);
  }
}

.pokeball::before,
.pokeball::after {
  content: "";
  position: absolute;
}

.pokeball::before {
  background: red;
  width: 100%;
  height: 50%;
}

.pokeball::after {
  top: calc(50% - 5px);
  width: 100%;
  height: 10px;
  background: #000;
}

.pokeball__button {
  position: absolute;
  top: calc(50% - 15px);
  left: calc(50% - 15px);
  width: 30px;
  height: 30px;
  background: #fff;
  border: 4px solid #7f8c8d;
  border-radius: 50%;
  z-index: 10;
  box-shadow: 0 0 0 7px black;
  -webkit-animation: blink 0.5s alternate infinite;
          animation: blink 0.5s alternate infinite;
  -webkit-animation-play-state: paused;
          animation-play-state: paused;
}

.pokeball:hover .pokeball__button {
  -webkit-animation-play-state: running;
          animation-play-state: running;
}

@-webkit-keyframes blink {
  from {
    background: #eee;
  }
  to {
    background: #e74c3c;
  }
}

@keyframes blink {
  from {
    background: #eee;
  }
  to {
    background: #e74c3c;
  }
}
/* Cards */
.container {
  position: relative;
  width: 300px;
  height: 400px;
  -webkit-perspective: 1000px;
          perspective: 1000px;
  -webkit-transform-style: preserve-3d;
          transform-style: preserve-3d;
}
.card {
  position: absolute;
  width: 100%;
  height: 100%;
  display: grid;
  place-items: center;
  border-left: rgba(255, 255, 255, 0.342) 1px solid;
  border-top: rgba(255, 255, 255, 0.342) 1px solid;
  border-radius: 20px;
  -webkit-backdrop-filter: blur(30px);
          backdrop-filter: blur(30px);
  box-shadow: rgba(0, 0, 0, 0.1) 0px 0px 20px;
  color: white;
  -webkit-backface-visibility: hidden;
          backface-visibility: hidden;
  -webkit-transform: rotateY(0deg);
          transform: rotateY(0deg);
  -webkit-transform-style: preserve-3d;
          transform-style: preserve-3d;
  transition: 2.5s cubic-bezier(0.4, 0.2, 0.2, 1);
}
.front {
  -webkit-transform: rotateY(0deg);
          transform: rotateY(0deg);
  padding: 6px;
}
.back {
  background: linear-gradient( 76.9deg,  rgba(255,90,90,1) 27.2%, rgba(130,5,255,1) 79.9% );
  -webkit-transform: rotateY(180deg);
          transform: rotateY(180deg);
  padding: 10px;
}
.container:hover > .front {
  -webkit-transform: rotateY(-180deg);
          transform: rotateY(-180deg);
}
.container:hover > .back {
  -webkit-transform: rotateY(0deg);
          transform: rotateY(0deg);
}
.btn {
  border: 1px solid white;
  background: rgba(255, 255, 255, 0.21);
  padding: 10px 20px;
  border-radius: 20px;
  text-decoration: none;
  color: white;
  transition: all 0.5s;
}
.btn:hover {
  padding: 10px 40px;
  background: white;
  color: black;
}
